<template>
  <div class="map">
    <div class="header">
      <div class="controls">
        <span @click="clickSeting" class="seting" :class="{ active: active === 'seting' }">设置</span>
        <span 
          @click="updateMap" 
          :class="{ active: active === 'update-map'}" 
          class="update-map-button">
          随机地图
        </span>
        <!-- <span @click="handlePath" class="minimal-path-button">最短路线</span> -->
        <span @click="clickContent( 'select-start-end' )" :class="{ active: active === 'select-start-end'}" class="minimal-path-button">起点和终点</span>
        <span 
          @click="clickMagic" 
          :class="{ 
            active: active === 'magic',
            disabled: start || end
          }" 
          class="minimal-path-button">魔法棒</span>
      </div>
      <div 
        class="info"
        :style="{
          width: rowNum * G + left * 2 + 'px'          
        }">
        <span :style="{ paddingLeft: left + 'px' }">用时: <span v-if="timing">{{ timing }} s</span></span>
        <span style="padding-left: 1rem;">start: {{ start }}</span>
        <span style="padding-left: 1rem;">end: {{ end }}</span>
      </div>
    </div>
    <div 
      class="content" 
      @click="clickContent" 
      :style="{
        width: rowNum * G + left * 2 + 'px',
        height: rowNum * G + top + 'px'
      }">
      <div 
        v-for="item, index in modelData" 
        :key="index"
        class="grid"
        :class="{
          start: item.type === 'start',
          end: item.type === 'end',
          roadblock: item.type === 'roadblock',
          passby: item.type === 'passby'
        }"
        :data-index="item.index"
        :style="{
          width: item.width + 'px',
          height: item.height + 'px',
          top: item.y + 'px',
          left: item.x + 'px'
        }">
        <div v-if="showFGH">
          {{ item.index }}
          <span style="color: #f02e33;">{{ item.F }}</span>
          {{ item.G }}
          {{ item.H }}
        </div>
      </div>
    </div>
    <div class="modal-dom" v-if="showModal">
      <div class="mask"></div>
      <div class="wrap">
        <div class="modal-content">
          <div class="modal-title">设置</div>
          <div class="modal-body">
            <div>
              <div>宽高:</div> 
              <input type="number" v-model="setG" :min="2" :max="100" /></div>
            <div>
              <div>行列数:</div> 
              <input type="number" v-model="setRowNum" :min="5" :max="1000" />
            </div>
            <div>
              <div>FGH:</div>
              <select v-model="setShowFGH">
                <option :value="true">是</option>
                <option :value="false">否</option>
              </select>
            </div>
          </div>  
          <div class="modal-footer">
            <div @click="showModal = false">取消</div>
            <div @click="ok">确认</div>
          </div>    
        </div>
      </div>
    </div>
  </div>
</template>
<script>
const isMobile = /mobile/gi.test( navigator.userAgent );

export default {
  name: 'm-map',
  data(){
    return {
      modelData: [],
      modelMap: new Map,
      openList: [],
      start: null,
      end: null,
      endBlock: null,
      startBlock: null,
      date: null,
      timing: 0,
      active: null,
      roadblock: [ 4, 5, 8, 12, 18, 19, 22, 23, 24, 26, 29, 34, 36, 37, 39, 44, 46, 47, 49, 54, 56, 57, 59, 62, 63, 64, 67, 69, 75, 79, 81, 82, 83, 85, 87, 89, 93, 95 ],
      left: 10,
      top: 0,
      G: isMobile ? 30 : 60,
      setG: isMobile ? 30 : 60,
      rowNum: 10,
      setRowNum: 10,
      showFGH: false,
      showModal: false,
      setShowFGH: false,
      showFGH: false
    }
  },

  mounted(){
    this.drawMap();
  },

  methods: {
    randomNum( min, max ){
      return Math.floor( Math.random() * ( max - min ) + min );
    },

    clickMagic(){
      if( this.start || this.end ) return;
      if( this.active === 'magic' ){
        this.active = '';
      }else{
        this.active = 'magic';  
      }
      
    },

    clickContent( e ){
      if( e === 'select-start-end' ){
        if( this.start || this.end ) return;
        this.active = 'select-start-end';
      }else{
        let { dataset } = e.target;
        if( dataset && dataset.index ){
          let index = Number( dataset.index );
          let block = this.modelMap.get( index );
          if( this.active === 'select-start-end' ){
            if( block.type !== 'roadblock' ){
              if( !this.start ){
                this.start = index;
              }else if( !this.end && index !== this.start ){
                this.active = null;
                this.end = index;
                this.endBlock = this.modelMap.get( this.end );
                this.endBlock.type = 'end';
                this.startBlock = this.modelMap.get( this.start )
                this.startBlock.type = 'start';
                this.date = Date.now();
                this.handlePath( this.modelMap.get( this.start ) );
              }
            }
          }else if( this.active === 'magic' ){
            if( !block.type ){
              block.type = 'roadblock';
            }else{
              block.type = '';
            }
          }
        }
      }
    },

    selectStartEnd(){
      
    },

    drawMap(){
      let modelData = this.compurGrid();
      for( let num of this.roadblock ){
        this.modelMap.get( num ).type = 'roadblock';
      }
      this.modelData = modelData;
    },

    compurGrid(){
      let { left, top, rowNum, G } = this;
      let modelData = [];
      for( let y = 0; y < rowNum; y ++ ){
        for( let x = 0; x < rowNum; x++ ){
          let index = x + 1 + y * rowNum;
          let obj = {
            width: G, 
            height: G,
            x: x * G + left,
            y: y * G + top,
            index,
            direction: [ 
              index - rowNum, 
              index % rowNum === 0 ? 0 : index + 1, 
              index + rowNum, 
              index % rowNum === 1 ? 0 : index - 1
            ],//N, W, S, E
          }
          modelData.push( obj );
          this.modelMap.set( obj.index, obj )
        }
      }
      return modelData;
    },

    initData(){
      this.start = null;
      this.end = null;
      this.timing = null;
      this.modelData = [];
      this.modelMap.clear();
    },

    updateMap(){
      this.active = 'update-map';
      this.initData();
      let modelData = this.compurGrid();
      let roadblock = new Array( this.rowNum * (this.rowNum - Math.floor( this.rowNum / 2 )) );
      let i = 0;
      for( let num of roadblock )
        this.modelMap.get( this.randomNum( 1, this.rowNum * this.rowNum ) ).type = 'roadblock';

      this.modelData = modelData;
      this.active = null;
    },

    handlePath(){
      this.date = Date.now();
      this.minimalPath( this.modelMap.get( this.start ) );
    },

    minimalPath( startBlock ){
      this.pathScoring( startBlock );
      let item, type;
      while( this.openList.length && type !== 'end' ){
        let openList = [ ...this.openList ];
        item = this.openList.pop();
        type = item.type;
        this.pathScoring( item );
      }

      if( item && item.type === 'end' ){
        this.drawPath( item );
        this.timing = ( Date.now() - this.date ) / 1000;
        this.$forceUpdate();
      }else{
        alert('无法到达终点!')
        this.updateMap();
      }
      
    },

    drawPath( block ){
      if( block.type !== 'end' && block.type !== 'start' ) block.type = 'passby';
      if( block.parent ){
        this.drawPath( this.modelMap.get( block.parent ) );
      }
    },

    computFGH( block, currentBlock ){
      let { x, y, index } = block;
      let H = Math.abs( x - this.endBlock.x ) + Math.abs( y - this.endBlock.y );
      let G;
      if( !currentBlock.G ){
        G = Math.abs( x - this.startBlock.x ) + Math.abs( y - this.startBlock.y );
      }else{
        G = currentBlock.G + this.G;
      }
      let F = H + G;
      block.H = H;
      block.G = G;
      block.F = F;
    },

    pathScoring( block ){
      let { width, index, x, y, direction } = block;
      for( let item of direction ){
        let borderUpon = this.modelMap.get( item );
        if( borderUpon && borderUpon.type !== 'roadblock' && borderUpon.type !== 'start' && borderUpon.type !== 'openList' ){
          this.computFGH( borderUpon, block );
          if( borderUpon.type !== 'end' ){
            borderUpon.type = 'openList';
          }

          borderUpon.parent = block.index;
          this.openList.push( borderUpon );
        }
      }
      this.openList.sort(( a, b ) => b.F - a.F );
    },

    clickSeting(){
      this.showModal = true;
      this.active = 'seting';
    },

    cancel(){
      this.active = null;
      this.showModal = false;
    },

    ok(){
      this.G = this.setG;
      this.rowNum = this.setRowNum;
      this.showFGH = this.setShowFGH;
      this.initData();
      this.drawMap();
      this.active = null;
      this.showModal = false;
    }
  }
}
</script>

